<template>
	<div class="newest">
		<div class="newest_box1">
			<div class="text">
				<div class="flex1">
					<div class="text" v-for="(item,index) in title" :key="index" @click="silde(index,item)">
						{{item.title}}
					</div>
					<div class="i" :class="{active:reg}"></div>
				</div>

				<div class="hr">

				</div>
			</div>
		</div>
		<div class="newest_box2" >
			<div class="box" >
				<div class="list" v-for="(item,index) in list" :key="index" >
					<router-link :to="{path:'/detail',query:{id:item.id}}" style="border-radius: 10px"><img :src="item.logo" />
						<div class="img_text">
							<div class="text">{{ item.title }}</div>
							<div class="flex">
								<div class="el-icon-time"><span
										style="font-size: 24px;color: red;">￥{{item.market_price}}</span><span
										style="color: #CCCCCC;text-decoration: line-through;">{{ item.discount_price}}</span>
								</div>
								<div class="el-icon-postcard"><span style="color: green;">立即查看</span></div>
							</div>
						</div>
					</router-link>
				</div>
			</div>
		</div>
		<!-- <div class="paging">
			<div>&lt;</div> -->
		<!-- <div v-for="(item,index) in 10" :key="index" v-show="index<=9">
				{{index+1}}
			</div> -->
		<!-- <div v-for="(item,index) in 10" :key="index">
