			</div>
			<div>&gt;</div>
		</div> -->
		<el-pagination v-show="page>1" class="paging" :page-size="1" :pager-count="11" layout="prev, pager, next"
			@current-change="get" :total="page">
		</el-pagination>

	</div>
</template>

<script>
	import {
		sort,
		product
	} from "../api/home";
	export default {
		props: {

		},
		data() {
			return {
				list: [],
				title: [],
				reg: false,
				page: 0,
			}
		},
		mounted() {
			sort().then(res => {
				this.title = res;
			}).catch(error => {

			});
			product().then(res => {
				this.list = res.list;
				this.page = res.page * 1;
			}).catch(error => {
				console.log(error);
			})
		},
		methods: {
			silde(index, item) {
				if (index == 0) {
					this.reg = false;

					var data = new Array();
					data.push({
						"cate_id": item.id
					});
					product(data[0]).then(res => {
						this.list = res.list;
						this.page = res.page * 1;
					}).catch(error => {
						console.log(error);
					})
				}
				if (index == 1) {
					this.reg = true;
					var data = new Array();
					data.push({
						"cate_id": item.id
					});
					product(data[0]).then(res => {
            // console.log(res)
            this.list = res.list;
            this.page = res.page * 1;
          }).catch(error => {
						console.log(error);
					})
				}
			},
			get(val) {
				var data = new Array();
				data.push({
					"page": val
				})
				product(data[0]).then(res => {
          // console.log(res.list);
          this.list = res.list;
        }).catch(error => {
					console.log(error);
				})
			}


		}
	}
</script>

